import axios from "axios";

import { getCookie, setCookie } from "./cookieUtil";
import { SERVER_URL } from "../api/config";

// intercepter  axios 
//     api  
//     
const jwtAxios = axios.create();

// (request) intercepter
// request   ,    
const beforeReq = config => {
  const memberInfo = getCookie("member");

  if (!memberInfo) {
    // console.log("  .");
    // axios  .
    return Promise.reject({ response: { data: { error: "Login ." } } });
  }

  const { accessToken } = memberInfo;
  // console.log("4.  ", accessToken);
  //  Request  headers   .
  // jwt    .
  // config   axios 
  //    axios   .
  //   headers .
  config.headers.Authorization = `Bearer ${accessToken}`;

  return config;
};

// fail Request   
const requestFail = err => {
  console.log("  .... ", err);
  return Promise.reject(err);
};

// Refresh Token
//      
export const refreshJWT = async (accessToken, refreshToken) => {
  const host = SERVER_URL;
  const header = { headers: { Authorization: `Bearer ${accessToken}` } };
  // API  Refresh  (URI) 
  const res = await axios.get(`${host}/api/parent/refresh-token`, header);

  console.log("1. refreshToken  ");
  //   AccessToken  RefereshToken 
  console.log("2.    ", res.data);
  return res.data;
};

// (Response)  
// Response 
const beforeRes = async res => {
  // console.log("Response  ....", res);
  const data = res.data;
  // console.log("1. Response    ", data);
  if (data && data.error === "ERROR_ACCESS_TOKEN") {
    // console.log("2.      !! . ");
    // console.log("3.    . ");
    // console.log("4.    ,  .");
    const memberInfo = getCookie("member");
    // console.log("5.    AccessToken ", memberInfo.accessToken);
    // console.log("6.    RefreshToken ", memberInfo.refreshToken);

    // console.log("7.     .");

    const result = await refreshJWT(
      memberInfo.accessToken,
      memberInfo.refreshToken,
    );
    console.log("8.        ");
    (memberInfo.accessToken = result.accessToken),
      (memberInfo.refreshToken = result.refreshToken),
      setCookie("member", JSON.stringify(memberInfo));

    // console.log("9.   API ");

    const originalRequest = res.config;
    originalRequest.headers.Authorization = `Bearer ${result.accessToken}`;

    return await axios(originalRequest);
  }

  return res;
};
// Response Fail 
const responseFail = err => {
  console.log("Response Fail Err", err);
  return Promise.reject(err);
};

// axios  
jwtAxios.interceptors.request.use(beforeReq, requestFail);
jwtAxios.interceptors.response.use(beforeRes, responseFail);

export default jwtAxios;
