import { createSlice } from "@reduxjs/toolkit";

// API  
// reducer (store  )    API 
import { createAsyncThunk } from "@reduxjs/toolkit";
import { getCookie, removeCookie, setCookie } from "../util/cookieUtil";
import { postParentLogin, postTeacherLogin } from "../api/user/userApi";

// export const  = createAsyncThunk("", );
export const loginPostAsync = createAsyncThunk(
  "loginPostAsync",
  async ({ loginParam, successFn, failFn, errorFn }) => {
    try {
      const res = await postTeacherLogin({
        loginParam,
        successFn,
        failFn,
        errorFn,
      });
      return res;
    } catch (error) {
      console.log(error);
    }
  },
);
export const postParentLoginAsync = createAsyncThunk(
  "postParentLoginAsync",
  async ({ loginParam, successFn, failFn, errorFn }) => {
    try {
      const res = await postParentLogin({
        loginParam,
        successFn,
        failFn,
        errorFn,
      });
      return res;
    } catch (error) {
      console.log(error);
    }
  },
);

const initState = {
  teacherUid: "",
  iparent: "",
};

//    initState 
const loadMemberCookie = () => {
  const memberInfo = getCookie("member");
  return memberInfo;
};
const loginSlice = createSlice({
  name: "loginSlice",
  initialState: loadMemberCookie() || initState,

  // store  state     
  reducers: {
    login: (state, action) => {
      // console.log("login.....");
      return {
        teacherUid: action.payload.teacherUid,
        iparent: action.payload.iparent,
      };
    },
    // 
    logout: (state, action) => {
      // console.log("logout.....");
      removeCookie("member", "/");
      return { ...initState };
    },
  },
  //  API   store  state    
  extraReducers: builder => {
    builder
      .addCase(loginPostAsync.fulfilled, (state, action) => {
        //   
        // state :  (store  loginSate)
        // action :  
        // console.log("fulfilled");
        const payload = action.payload;
        console.log(action);
        if (payload) {
          // 
          setCookie("member", JSON.stringify(payload));
        }
        return payload;
      })
      .addCase(loginPostAsync.pending, (state, action) => {
        //   ..
        // state :  (store  loginSate)
        // action :  
        // console.log("pending");
      })
      .addCase(loginPostAsync.rejected, (state, action) => {
        //   
        // state :  (store  loginSate)
        // action :  
        // console.log("rejected");
      })
      .addCase(postParentLoginAsync.fulfilled, (state, action) => {
        //   
        // state :  (store  loginSate)
        // action :  
        // console.log("fulfilled");
        const payload = action.payload;
        console.log(action);
        if (payload) {
          // 
          setCookie("member", JSON.stringify(payload));
        }
        return payload;
      })
      .addCase(postParentLoginAsync.pending, (state, action) => {
        //   ..
        // state :  (store  loginSate)
        // action :  
        // console.log("pending");
      })
      .addCase(postParentLoginAsync.rejected, (state, action) => {
        //   
        // state :  (store  loginSate)
        // action :  
        console.log("rejected");
      });
  },
});

export const { login, logout } = loginSlice.actions;
export default loginSlice.reducer;
