import React, { lazy, Suspense } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import "./styles/index.css";
import "./styles/normalize.css";

//    
import Loading from "./components/loading/Loading";
import ContentLayout from "./layouts/common/ContentLayout";
import NotFound from "./pages/NotFound";

// lazy    

//  
const MainLayout = lazy(() => import("./layouts/MainLayout"));

// 
const MainPage = lazy(() => import("./pages/Main"));

// ,  
const Login = lazy(() => import("./pages/user/Login"));
//  
const Accounts = lazy(() => import("./pages/user/Accounts"));
// 
const SignupForm = lazy(() => import("./pages/user/SignupForm"));
const MyPage = lazy(() => import("./pages/user/MyPage"));
//
const Withdraw = lazy(() => import("./pages/user/Withdraw"));

//   
const Info = lazy(() => import("./pages/information/Info"));
const InfoClass = lazy(() => import("./pages/information/InfoClass"));
const Location = lazy(() => import("./pages/information/Location"));

//  
const Edu = lazy(() => import("./pages/education/Edu"));
const SpecialAct = lazy(() => import("./pages/education/SpecialAct"));

//  
const Album = lazy(() => import("./pages/album/Album"));
const AlbumDetails = lazy(() => import("./pages/album/AlbumDetails"));
const AlbumModify = lazy(() => import("./pages/album/AlbumModify"));
const AlbumWrite = lazy(() => import("./pages/album/AlbumWrite"));

//  ()
const NoticeDetails = lazy(() => import("./pages/notice/NoticeDetails"));
const NoticeList = lazy(() => import("./pages/notice/NoticeList"));
const NoticeModify = lazy(() => import("./pages/notice/NoticeModify"));
const NoticeWrite = lazy(() => import("./pages/notice/NoticeWrite"));

// 
const GuardianList = lazy(() => import("./pages/adminPage/GuardianList"));
const StudCreate = lazy(() => import("./pages/adminPage/student/StudCreate"));
const StudDetailsForm = lazy(() =>
  import("./pages/adminPage/student/StudDetailsForm"),
);
const StudDetails = lazy(() => import("./pages/adminPage/student/StudDetails"));
const StudList = lazy(() => import("./pages/adminPage/student/StudList"));

// 
const IndivNotiModify = lazy(() =>
  import("./pages/individualNotice/IndivNotiModify"),
);

const IndivNotiHistory = lazy(() =>
  import("./pages/individualNotice/IndivNotiHistory"),
);

const IndivNotiList = lazy(() =>
  import("./pages/individualNotice/IndivNotiList"),
);

function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route
          path="/"
          element={
            <Suspense fallback={<Loading />}>
              <MainLayout />
            </Suspense>
          }
        >
          <Route
            index
            element={
              <Suspense fallback={<Loading />}>
                <MainPage />
              </Suspense>
            }
          />
          {/* </Route> */}
          <Route
            path="user"
            element={
              <Suspense fallback={<Loading />}>
                <ContentLayout />
              </Suspense>
            }
          >
            {/* user pages    */}
            {/*  */}
            <Route
              index
              path="accounts"
              element={
                <Suspense fallback={<Loading />}>
                  <Accounts />
                </Suspense>
              }
            />
            <Route
              index
              path="signup"
              element={
                <Suspense fallback={<Loading />}>
                  <SignupForm />
                </Suspense>
              }
            />
            {/*    */}
          </Route>

          {/*   */}
          <Route
            path="login"
            element={
              <Suspense fallback={<Loading />}>
                <Login />
              </Suspense>
            }
          />
          {/*  */}
          <Route
            path="myPage"
            element={
              <Suspense fallback={<Loading />}>
                <MyPage />
              </Suspense>
            }
          />
          {/*  */}
          <Route
            path="withdraw"
            element={
              <Suspense fallback={<Loading />}>
                <Withdraw />
              </Suspense>
            }
          />

          {/*    */}
          <Route
            path="info"
            element={
              <Suspense fallback={<Loading />}>
                <ContentLayout />
              </Suspense>
            }
          >
            {/*   */}
            <Route
              index
              element={
                <Suspense fallback={<Loading />}>
                  <Info />
                </Suspense>
              }
            />
            {/*   */}
            <Route
              path="class"
              element={
                <Suspense fallback={<Loading />}>
                  <InfoClass />
                </Suspense>
              }
            />
            {/*   */}
            <Route
              path="location"
              element={
                <Suspense fallback={<Loading />}>
                  <Location />
                </Suspense>
              }
            />
          </Route>
          {/* */}
          <Route
            path="edu"
            element={
              <Suspense fallback={<Loading />}>
                <ContentLayout />
              </Suspense>
            }
          >
            {/*   */}
            <Route
              index
              element={
                <Suspense fallback={<Loading />}>
                  <Edu />
                </Suspense>
              }
            />
            {/*    */}
            <Route
              path="specialact"
              element={
                <Suspense fallback={<Loading />}>
                  <SpecialAct />
                </Suspense>
              }
            />
          </Route>
          {/*  */}
          <Route
            path="album"
            element={
              <Suspense fallback={<Loading />}>
                <ContentLayout />
              </Suspense>
            }
          >
            {/*   */}
            <Route
              index
              element={
                <Suspense fallback={<Loading />}>
                  <Album />
                </Suspense>
              }
            />
            {/*   */}
            <Route
              path="details/:pno"
              element={
                <Suspense fallback={<Loading />}>
                  <AlbumDetails />
                </Suspense>
              }
            />
            {/*   */}
            <Route
              path="write"
              element={
                <Suspense fallback={<Loading />}>
                  <AlbumWrite />
                </Suspense>
              }
            />
            {/*   */}
            <Route
              path="modify/:pno"
              element={
                <Suspense fallback={<Loading />}>
                  <AlbumModify />
                </Suspense>
              }
            />
          </Route>
          {/*   */}
          <Route
            path="notice"
            element={
              <Suspense fallback={<Loading />}>
                <ContentLayout />
              </Suspense>
            }
          >
            <Route
              index
              element={
                <Suspense fallback={<Loading />}>
                  <NoticeList />
                </Suspense>
              }
            />
            <Route
              path="write"
              element={
                <Suspense fallback={<Loading />}>
                  <NoticeWrite />
                </Suspense>
              }
            />
            <Route
              path="/notice/details/:tno"
              element={
                <Suspense fallback={<Loading />}>
                  <NoticeDetails />
                </Suspense>
              }
            />
            <Route
              path="/notice/modify/:tno"
              element={
                <Suspense fallback={<Loading />}>
                  <NoticeModify />
                </Suspense>
              }
            />
          </Route>
          {/*  */}

          <Route
            path="admin"
            element={
              <Suspense fallback={<Loading />}>
                <ContentLayout />
              </Suspense>
            }
          >
            {/*    */}
            <Route
              index
              element={
                <Suspense fallback={<Loading />}>
                  <GuardianList />
                </Suspense>
              }
            />
            <Route
              path="student/detailsform"
              element={
                <Suspense fallback={<Loading />}>
                  <StudDetailsForm />
                </Suspense>
              }
            />
            <Route
              path="student/create"
              element={
                <Suspense fallback={<Loading />}>
                  <StudCreate />
                </Suspense>
              }
            />
            <Route
              path="student/list"
              element={
                <Suspense fallback={<Loading />}>
                  <StudList />
                </Suspense>
              }
            />
            <Route
              path="student/details"
              element={
                <Suspense fallback={<Loading />}>
                  <StudDetails />
                </Suspense>
              }
            />
          </Route>
          {/*   */}
          <Route
            path="ind"
            element={
              <Suspense fallback={<Loading />}>
                <ContentLayout />
              </Suspense>
            }
          >
            <Route
              index
              element={
                <Suspense fallback={<Loading />}>
                  <IndivNotiList />
                </Suspense>
              }
            />
            <Route
              path="history"
              element={
                <Suspense fallback={<Loading />}>
                  <IndivNotiHistory />
                </Suspense>
              }
            />
            <Route
              path="form"
              element={
                <Suspense fallback={<Loading />}>
                  <IndivNotiModify />
                </Suspense>
              }
            />
          </Route>
          <Route path="*" element={<NotFound />}></Route>
          {/* </Route> */}
        </Route>
      </Routes>
    </BrowserRouter>
  );
}

export default App;
